@charset "UTF-8";
.form:after {
  content: "";
  display: table;
  clear: both;
}

input {
  line-height: 28px;
  box-shadow: none;
  border-radius: 0;
  appearance: none;
}

input[type="file"], input[type="button"], input[type="submit"], input[type="reset"] {
  appearance: none;
}

input::placeholder {
  color: #888;
}

input:focus::placeholder {
  color: rgba(136, 136, 136, 0.5);
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  appearance: none !important;
}

select, textarea {
  border-radius: 0;
  appearance: none;
  line-height: 28px;
}

textarea {
  width: 100%;
  max-width: 100%;
  padding: 5px;
  box-sizing: border-box;
  border: 1px solid #c0c0c0;
}

select::-ms-expand {
  display: none;
}

/* form   */
.form__legend {
  padding: 10px;
  font-weight: bold;
  font-size: 24px;
  font-size: 1.5rem;
  line-height: 24px;
  line-height: 1.5rem;
}

.form__legend--hide {
  display: none;
}

.form__legend-icon {
  margin-right: 10px;
  display: inline-block;
  vertical-align: middle;
}

.form__group {
  padding: 5px 0;
}

.form__label {
  padding: 10px 10px 0 10px;
}

.form__label--hide {
  display: none;
}

.form__label--right {
  text-align: right;
}

.form__label .form__title {
  padding: 11px 0 0 15px;
  display: inline-block;
  position: relative;
}

.form__label--required .form__title::before {
  content: "*";
  font-weight: bold;
  color: #c00000;
  display: inline-block;
  vertical-align: middle;
  position: absolute;
  left: 0;
  top: 11px;
}

.form__field {
  padding: 10px;
  box-sizing: border-box;
}

.form__calendar {
  position: relative;
}

.form__control {
  width: 100%;
  height: 50px;
  padding: 11px 10px;
  box-sizing: border-box;
  color: #252525;
  border: 1px solid #c0c0c0;
  background: #f3f3f3;
  border-radius: 10px;
  position: relative;
}

.form__control.input-validation-error {
  border-color: #c00000;
}

.form__control.input-validation-success {
  border-color: #1e9813;
}

.form__control[disabled], .form__control[readonly] {
  cursor: not-allowed;
  filter: opacity(65%);
  box-shadow: none;
  opacity: .65;
  background: #f3f3f3;
}

.form__select {
  position: relative;
  padding: 0;
}

.form__select select {
  width: calc(100% + 1px*2);
  height: calc(100% + 1px*2);
  margin: -1px;
  padding: 0 10px;
  box-sizing: border-box;
  background-color: rgba(0, 0, 0, 0);
  border-width: 0;
  border-radius: 10px;
  position: relative;
  z-index: 2;
}

.form__select select option {
  color: #252525;
}

.form__select select.input-validation-error {
  border: 1px solid #c00000;
}

.form__select select.input-validation-success {
  border: 1px solid #1e9813;
}

.form__select::after {
  content: "";
  margin: auto;
  height: 0;
  width: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-top: 10px solid #555;
  position: absolute;
  top: 0;
  right: 10px;
  bottom: 0;
  z-index: 1;
}

.form__textarea {
  height: 150px;
}

.form__feedback, .form__tips {
  padding: 5px 10px 0 10px;
  display: block;
  color: #999;
}

.form__feedback::before, .form__tips::before {
  font-family: 'icomoon';
  margin: -2px 5px 0 0;
  display: inline-block;
  vertical-align: middle;
}

.form__tips::before {
  content: "";
  font-size: 20px;
}

.form__tips span {
  display: inline-block;
  vertical-align: middle;
}

.form__tips a {
  color: #168800;
}

.form .field-validation-valid {
  display: none;
}

.form .field-validation-valid::before {
  content: "";
}

.form .field-validation-success {
  color: #1e9813;
  display: block;
}

.form .field-validation-success::before {
  content: "";
}

.form .field-validation-error {
  color: #c00000;
  display: block;
}

.form .field-validation-error::before {
  content: "";
}

.form__img {
  display: inline-block;
  vertical-align: middle;
  padding: 11px 0;
}

.form__img img {
  width: auto;
  height: 28px;
  margin-right: 10px;
  display: inline-block;
}

.form__reset {
  display: inline-block;
  vertical-align: middle;
  color: #1e9813;
}

.form__reset i {
  font-size: 28px;
  display: inline-block;
  transition: all .5s ease-out;
}

.form__reset:hover {
  color: #1e9813;
}

.form__reset:hover i {
  transform: rotate(180deg);
}

input[type=radio] {
  vertical-align: middle;
}

input[type=checkbox] {
  vertical-align: middle;
}

.form-selection {
  padding: 5px 0;
  box-sizing: border-box;
}

.form-selection__input {
  opacity: 0;
  position: absolute;
}

.form-selection--inline {
  margin-right: 15px;
  display: inline-block;
  max-width: initial;
}

.form-selection__label {
  cursor: pointer;
  display: block;
}

.form-selection__text {
  position: relative;
  vertical-align: middle;
  padding: 5px 10px 5px 0;
  font-weight: normal;
  max-width: initial;
}

.form-selection__text .form-selection__option, .form-selection__text .form-selection__note {
  display: inline-block;
  vertical-align: top;
}

.form-selection__text .form-selection__option {
  margin-right: 5px;
  position: relative;
}

.form-selection__text .form-selection__option::before {
  content: '';
  position: absolute;
  transition: all .3s ease-out;
  display: none;
}

.form-selection__box {
  padding-top: 5px;
  padding-left: 23px;
  display: none;
}

.form-selection__text--radiobox {
  line-height: 23px;
}

.form-selection__text--radiobox .form-selection__option, .form-selection__text--radiobox .form-selection__option::before {
  border-radius: 23px;
}

.form-selection__text--radiobox .form-selection__option {
  width: 23px;
  height: 23px;
  background-color: #f3f3f3;
  border: 1px solid #c0c0c0;
}

.form-selection__text--radiobox .form-selection__option::before {
  width: 13px;
  height: 13px;
  margin: auto;
  background: #555;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.form-selection__text--checkbox {
  line-height: 23px;
}

.form-selection__text--checkbox .form-selection__option, .form-selection__text--checkbox .form-selection__note::before {
  border-radius: 3px;
}

.form-selection__text--checkbox .form-selection__option {
  width: 23px;
  height: 23px;
  background-color: #f3f3f3;
  border: 1px solid #c0c0c0;
}

.form-selection__text--checkbox .form-selection__option::before {
  width: 13px;
  height: 7.659px;
  border-style: solid;
  border-color: #555;
  border-width: 0 0 3px 3px;
  margin: auto;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transform: rotate(-45deg) translate(2px, -2px);
}

.form-selection__text--btn {
  width: 150px;
  height: 50px;
  padding: 11px 10px;
  border: 1px solid #c0c0c0;
  background-color: #f3f3f3;
  color: #555;
  border-radius: 5px;
  text-align: center;
}

.form-selection__input:checked + .form-selection__label > .form-selection__text--radiobox .form-selection__option::before,
.form-selection__input:checked + .form-selection__label > .form-selection__text--checkbox .form-selection__option::before {
  display: block;
}

.form-selection__input:checked + .form-selection__label .form-selection__text--btn {
  border: 1px solid #1e9813;
  background-color: #1e9813;
  color: #fff;
}

.form-selection__input:checked + .form-selection__label > .form-selection__box {
  display: flex;
}

.form-selection__input[disabled] {
  cursor: not-allowed;
}

.form-selection__input[disabled] + .form-selection__label {
  filter: opacity(50%);
  opacity: 0.4;
  cursor: not-allowed;
}
