//This is sass file
//background 設定
@mixin bg($img, $pos-x, $pos-y, $rep) {
	background-image: url("../images/#{$img}");
	background-position: $pos-x $pos-y;
	background-repeat: $rep; }
//rwd point
@mixin rwd($point) {
	@media only screen and (max-width: $point) {
		@content; } }
@mixin rwd2($point2) {
	@media only screen and (min-width: $point2) {
		@content; } }
//calc
@mixin calc($property, $expression) {
	#{$property}: -moz-calc(#{$expression});
	#{$property}: -webkit-calc(#{$expression});
	#{$property}: calc(#{$expression}); }
//+bficon(30,30,'\f138')
@mixin bficon($width,$height,$content,$font-size:100%,$top:0,$left:0) {
	position: relative;
	&:before {
		content: $content;
		position: absolute;
		top: $top;
		left: $left;
		width: $width;
		height: $height;
		font-family: 'icomoon';
		font-size: $font-size;
		@content; } }
//文字框線
@mixin stroke($s-width, $color) {
	$s-width: $s-width;
	$shadow: 0 0 0 transparent;
	$i: 0;
	$w: 1;
	@while ($s-width > $i) {
		$i: $i + 1;
		$j: 0;
		$w: $w + 2;
		@for $r from 1 through $w {
			@for $c from 1 through $w {
				$x: $c - ceil($w / 2);
				$y: $r - ceil($w / 2);
				$shadow: #{$shadow}, #{$x}px #{$y}px 0 $color; } } }
	text-shadow: $shadow; }

//Animate Mixin Plugin
@mixin animate($name: fadeIn, $duration: 1s, $delay: .2s, $function: ease, $mode: both) {
	@include experimental(animation, $name $duration $delay $function $mode); }
//case mixin
@mixin inner($inr-w) {
	max-width: $inr-w;
	@include rwd($inr-w) {
		padding: 0 15px; } }

//文字列高度 '...'
@mixin Ellipsis($ellipsis_lineHeight, $ellipsis_lineCount, $ellipsis_bgColor) {
	overflow: hidden;
	position: relative;
	line-height: $ellipsis_lineHeight;
	max-height: $ellipsis_lineHeight * $ellipsis_lineCount;
	padding-right: 1em;
	&:before {
		content: '...';
		position: absolute;
		right: 0;
		bottom: 0; }
	&:after {
		content: '';
		position: absolute;
		right: 0;
		width: 1rem;
		height: 1rem;
		margin-top: 0.2rem;
		background: $ellipsis_bgColor; } }
//文字轉換 rem
@mixin font-size($newSize) {
	$remSize: $newSize / $fontSz;
	$remlineHeight:  $fontLh * $newSize / ( $fontSz * $fontSz );
	font-size: $newSize;
	font-size: #{$remSize}rem;
	line-height: #{$remlineHeight}rem; }
//rem算式
@function toRem($size) {
	$remSize: $size / $fontSz;
	@return $remSize * 1rem; }
//文字行高轉換 rem
@mixin font-size2($newSize2,$newLineHeight) {
	font-size: $newSize2;
	font-size: toRem($newSize2);
	line-height: $newLineHeight;
	line-height: toRem($newLineHeight); }

// triangle 三角形 mixin
@mixin triangle ($size, $color, $direction) {
	height: 0;
	width: 0;
	$width: nth($size, 1);
	$height: nth($size, length($size));
	$foreground-color: nth($color, 1);
	$background-color: if(length($color) == 2, nth($color, 2), transparent);
	@if ($direction == up) or ($direction == down) or ($direction == right) or ($direction == left) {
		$width: $width / 2;
		$height: if(length($size) > 1, $height, $height/2);
		@if $direction == up {
			border-bottom: $height solid $foreground-color;
			border-left: $width solid $background-color;
			border-right: $width solid $background-color; }

		@else if $direction == right {
			border-bottom: $width solid $background-color;
			border-left: $height solid $foreground-color;
			border-top: $width solid $background-color; }
		@else if $direction == down {
			border-left: $width solid $background-color;
			border-right: $width solid $background-color;
			border-top: $height solid $foreground-color; }
		@else if $direction == left {
			border-bottom: $width solid $background-color;
			border-right: $height solid $foreground-color;
			border-top: $width solid $background-color; } }
	@else if ($direction == up-right) or ($direction == up-left) {
		border-top: $height solid $foreground-color;
		@if $direction == up-right {
			border-left:  $width solid $background-color; }
		@else if $direction == up-left {
			border-right: $width solid $background-color; } }
	@else if ($direction == down-right) or ($direction == down-left) {
		border-bottom: $height solid $foreground-color;
		@if $direction == down-right {
			border-left:  $width solid $background-color; }
		@else if $direction == down-left {
			border-right: $width solid $background-color; } }
	@else if ($direction == inset-up) {
		border-color: $background-color $background-color $foreground-color;
		border-style: solid;
		border-width: $height $width; }
	@else if ($direction == inset-down) {
		border-color: $foreground-color $background-color $background-color;
		border-style: solid;
		border-width: $height $width; }
	@else if ($direction == inset-right) {
		border-color: $background-color $background-color $background-color $foreground-color;
		border-style: solid;
		border-width: $width $height; }
	@else if ($direction == inset-left) {
		border-color: $background-color $foreground-color $background-color $background-color;
		border-style: solid;
		border-width: $width $height; } }

//置中 'v' 垂直置中 'h' 水平置中 'b' 垂直水平置中
@mixin center($position) {
	position: absolute;
	@if $position == 'v' {
		top: 50%;
		transform: translateY(-50%); }
	@else if $position == 'h' {
		left: 50%;
		transform: translate(-50%); }
	@else if $position == 'b' {
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%); } }
//+iconpc(b,'\f138')
//前面 加小圖示並置中-絶對位置 (要設定置中位置、圖示內容)
//置中位置 'b'= 表示 水平垂直置中, 'x'= 表示 水平置中, 'y'= 表示 垂直置中
//+iconpc(b,'\f138',0,100%,inherit,auto,auto)
@mixin iconpc($position,$content,$number:0,$font-size:100%,$color:inherit,$width:auto,$height:auto) {
	position: relative;
	@if $position == 'b' {
		&:before {
			content: $content;
			position: absolute;
			top: 50%;
			left: 50%;
			width: $width;
			height: $height;
			font-family: 'icomoon';
			font-size: $font-size;
			color: $color;
			transform: translate(-50%, -50%); } }
	@else if $position == 'x' {
		&:before {
			content: $content;
			position: absolute;
			top: $number;
			left: 50%;
			width: $width;
			height: $height;
			font-family: 'icomoon';
			font-size: $font-size;
			color: $color;
			transform: translateX(-50%); } }
	@else if $position == 'y' {
		&:before {
			content: $content;
			position: absolute;
			top: 50%;
			left: $number;
			width: $width;
			height: $height;
			font-family: 'icomoon';
			font-size: $font-size;
			color: $color;
			transform: translateY(-50%); } } }
